import React, { Component } from 'react';
import { RouteComponentProps, Route, NavLink } from "react-router-dom";
import firebase from 'firebase';
import { AppBar, Button, CssBaseline, Paper, Tab, Tabs, Toolbar, Typography, StyledComponentProps, WithTheme } from '@material-ui/core';
import { Theme, withStyles } from '@material-ui/core/styles';
import KhsLogo from './assets/images/khslogo.png';

import StatisticsPage from './pages/StatisticsPage';
import GigsPage from './pages/GigsPage';
import LoginForm from './components/LoginForm';

const styles = (theme: Theme) => ({
  root: {

  },
  grow: {
    flexGrow: 1,
  },
  appBar: {
  },
  appBarSpacer: {
    minHeight: theme.mixins.toolbar.height,
  },
  content: {
  }
});

class App extends Component<any, any> {
  state = {
    signInData: {
      email: '',
      password: '',
    },
    isSignedIn: false,
    selectedTab: 0,
  }

  // Listen to the Firebase Auth state and set the local state.
  componentDidMount() {
    firebase.auth().onAuthStateChanged((user: any) => {
      this.setState({isSignedIn: !!user});
    });
    switch (this.props.location.pathname) {
      case '/gigs':
        this.setState({selectedTab: 1});
        break;
      default:
        this.setState({selectedTab: 0});
    }
  }

  componentWillReceiveProps(newProps: any) {
  }

  // Make sure we un-register Firebase observers when the component unmounts.
  componentWillUnmount() {
  }

  handleTabChange = (evt: any, selectedTab: any) => {
    this.setState({ selectedTab });
  }

  handleSignInFieldChange = (name: string, value: string) => {
    this.setState((prevState: any) => ({
        ...prevState,
        signInData: {
            ...prevState.signInData,
            [name]: value,
        },
    }));
}

  handleSignIn = () => {
    const { email, password } = this.state.signInData;
    if (email === '' || password === '') { return; }
    firebase.auth().signInWithEmailAndPassword(email, password).then(user => {
      if (!user) { return }
      this.setState({isSignedIn: true});
    });
  }
  
  render() {
    const { classes } = this.props;
    const { signInData, isSignedIn, selectedTab } = this.state;
    if (!isSignedIn) {
      return (
        <LoginForm data={signInData} onFieldChange={this.handleSignInFieldChange} onSignIn={this.handleSignIn} />
      );
    }
    const LinkToStatistics = (props: any) => <NavLink to="/" {...props} />;
    const LinkToGigs = (props: any) => <NavLink to="/gigs" {...props} />;
    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar position="sticky" className={classes.appBar}>
          <Toolbar variant="dense" color="primary">
            <div className={classes.grow}>
              <img src={KhsLogo} />
            </div>
            {/* <Typography color="inherit" variant="h5" className={classes.grow}>
              Admin
            </Typography> */}
            <Button color="inherit" onClick={() => firebase.auth().signOut()}>
              Ulos
            </Button>
          </Toolbar>
          <Toolbar variant="dense">
            <Tabs
              onChange={this.handleTabChange}
              value={selectedTab}
              centered
            >
              <Tab label="Kävijät" component={LinkToStatistics} />
              <Tab label="Keikat" component={LinkToGigs} />
            </Tabs>
          </Toolbar>
        </AppBar>
        <main className={classes.content}>
          <div className="appBarSpacer" />
          <Route exact path="/" component={StatisticsPage} />
          <Route path="/gigs" component={GigsPage} />
        </main>
        <footer>
        </footer>
      </div>
    );
  }
}

export default withStyles(styles, {withTheme: true})(App);
