import React from 'react';
import {
    Button,
    Grid,
    Paper,
    TextField,
    Theme,
    Typography,
    withStyles,
} from '@material-ui/core';
import Logo from '../assets/images/khslogo.png';

interface IProps {
    data: any;
    onFieldChange: (fieldName: string, value: any) => void;
    onSignIn: () => void;
}

const styles = (theme: Theme) => ({
    root: {
        maxWidth: '300px',
        paddingTop: theme.spacing.unit * 5,
        margin: 'auto',
    },
    header: {
        padding: theme.spacing.unit * 3,
        backgroundColor: theme.palette.grey[900],
        color: theme.palette.common.white,
    },
    paper: {
        padding: theme.spacing.unit * 3,
    },
    actionButtons: {
        paddingTop: theme.spacing.unit * 2,
        display: 'flex',
    },
    loginButton: {
        flex: 1,
    },
    siteButton: {
        flex: 1,
    },
});

const LoginForm = (props: any) => {
    const { classes, data, onFieldChange, onSignIn } = props;
    if (!data) return null;
    return (
        <div className={classes.root}>
            <div className={classes.header}>
                <img src={Logo} />
                <Typography color="inherit" variant="subtitle1">Admin sivusto</Typography>
            </div>
            <Paper className={classes.paper}>
                <TextField
                    name="email"
                    label="Sähköposti"
                    type="email"
                    fullWidth
                    value={data.email || ''}
                    onChange={(evt: any) => onFieldChange(evt.target.name, evt.target.value)}
                />
                <TextField
                    name="password"
                    label="Salasana"
                    type="password"
                    fullWidth
                    value={data.password || ''}
                    onChange={(evt: any) => onFieldChange(evt.target.name, evt.target.value)}
                />
                <Grid container justify="space-between" className={classes.actionButtons}>
                    <Grid item>
                        <Button
                            className={classes.loginButton} 
                            variant="contained" 
                            color="primary" 
                            onClick={onSignIn}
                            disabled={data.email === '' || data.password === ''}
                        >
                            Kirjaudu
                        </Button>
                    </Grid>
                    <Grid item>
                        <Button
                            href="https://heltterit.com"
                            className={classes.siteButton}
                        >
                            heltterit.com
                        </Button>
                    </Grid>
                </Grid>
            </Paper>
        </div>
    );
};

export default withStyles(styles, {withTheme: true})(LoginForm);