import React, { Component } from 'react';
import firebase from 'firebase';
import { Avatar, Button, CircularProgress, List, ListItem, ListItemText, Table, TableBody, TableCell, TableHead, TableRow, Typography } from '@material-ui/core';
import { Theme, withStyles } from '@material-ui/core/styles';
import OpenIcon from '@material-ui/icons/ArrowForwardIos';
import red from '@material-ui/core/colors/red';
import grey from '@material-ui/core/colors/grey';

import GigDialog from '../components/GigDialog';

interface IGig {
    id?: string;
    desc: string;
    eventDate: string;
    eventType: string;
    venueAddress: string;
    venueCity: string;
    venueImgUrl: string;
    venueName: string;
    venueUrl: string;
}

const styles = (theme: Theme) => ({
    root: {
        // padding: theme.spacing.unit * 3,
    },
    electricAvatar: {
        color: red[500],
        backgroundColor: grey[900],
    },
    openIcon: {
        color: grey[400],
    },
    addButton: {
        marginTop: theme.spacing.unit * 2,
        marginLeft: theme.spacing.unit * 2,
    },
    loadingContainer: {
        marginTop: theme.spacing.unit * 2,
        marginLeft: theme.spacing.unit * 2,
    }
});

interface IState {
    gigs: IGig[],
    gigToEdit?: IGig,
    loading: boolean,
}

const getISODateString = (dateValue: Date): string => {
    const y = dateValue.getFullYear().toString();
    let m = (dateValue.getMonth() + 1).toString();
    let d = dateValue.getDate().toString();
    if (m.length === 1) { m = `0${m}`; }
    if (d.length === 1) { d = `0${d}`; }
    return `${y}-${m}-${d}`;
};

class Gig implements IGig {
    public readonly id?: string = undefined;
    public desc: string = '';
    public eventDate: string = getISODateString(new Date());
    public eventType: string = 'electric';
    public venueAddress: string = '';
    public venueCity: string = '';
    public venueImgUrl: string = '';
    public venueName: string = '';
    public venueUrl: string = '';

    constructor(gig?: IGig) {
        if (!!gig) {
            this.id = gig.id;
            this.desc = gig.desc;
            this.eventDate = gig.eventDate;
            this.eventType = gig.eventType;
            this.venueAddress = gig.venueAddress;
            this.venueCity = gig.venueCity;
            this.venueImgUrl = gig.venueImgUrl;
            this.venueName = gig.venueName;
            this.venueUrl = gig.venueUrl;    
        }
    }
}

class GigsPage extends Component<any, IState> {
    state: IState = {
        gigs: [],
        gigToEdit: undefined,
        loading: false,
    }

    sortGigs = (gigs: IGig[]): IGig[] => {
        return gigs.sort((a: any, b: any) => {
            if (a.eventDate > b.eventDate) return -1;
            if (a.eventDate < b.eventDate) return 1;
            return 0;
        });
    }

    componentDidMount() {
        this.setState({loading: true}, () => {
            firebase.database().ref('events').orderByChild('eventDate').once('value').then(snapshot => {
                let gigs: any[] = [];
                snapshot.forEach((gig: any) => {
                    // gigs.push(gig.val());
                    gigs.push({
                        ...gig.val(),
                        id: gig.key,
                    });
                })
                gigs = this.sortGigs(gigs);
                this.setState({ gigs, loading: false });
            });    
        });
    }

    handleEdit = (gig: IGig) => {
        this.setState((prevState: IState) => ({
            ...prevState,
            gigToEdit: new Gig(gig),
        }));
    }

    handleEditFieldChange = (name: string, value: any) => {
        this.setState((prevState: any) => ({
            ...prevState,
            gigToEdit: {
                ...prevState.gigToEdit,
                [name]: value,
            },
        }));
    }

    getUpdateObject = (gig: IGig) => {
        const retVal = {
            id: gig.id || null,
            desc: gig.desc !== undefined && gig.desc !== '' ? gig.desc : null,
            eventDate: gig.eventDate,
            eventType: gig.eventType,
            venueAddress: gig.venueAddress !== undefined && gig.venueAddress !== '' ? gig.venueAddress : null,
            venueCity: gig.venueCity !== undefined && gig.venueCity !== '' ? gig.venueCity : null,
            venueImgUrl: gig.venueImgUrl !== undefined && gig.venueImgUrl !== '' ? gig.venueImgUrl : null,
            venueName: gig.venueName,
            venueUrl: gig.venueUrl !== undefined && gig.venueUrl !== '' ? gig.venueUrl : null,
        };
        return retVal;
    }

    handleEditSave = () => {
        const { gigToEdit } = this.state;
        if (!gigToEdit) { return; }
        let addMode: boolean = false;
        if (!gigToEdit.id) {
            addMode = true;
            const myRef = firebase.database().ref('/events').push();
            gigToEdit.id = myRef.key || '';
        }
        if (!gigToEdit.id) { return; }
        const gigToUpdate = this.getUpdateObject(gigToEdit);
        firebase.database().ref(`events/${gigToUpdate.id}`).update(
            gigToUpdate,
            (err: any) => {
                this.setState((prevState: IState) => {
                    let newGigs = prevState.gigs;
                    if (addMode) {
                        newGigs.push(gigToEdit);
                    } else {
                        newGigs = newGigs.map((gig: IGig) => {
                            if (gig.id === gigToEdit.id) { return gigToEdit };
                            return gig;
                        });
                    }
                    return {
                        ...prevState,
                        gigs: this.sortGigs(newGigs),
                        gigToEdit: undefined,    
                    };
                });
            },
        );
    }

    handleEditCancel = () => {
        this.setState({gigToEdit: undefined});
    }

    handleDelete = () => {
        if (!this.state.gigToEdit) { return; }
        const { id, eventDate, venueName, venueCity } = this.state.gigToEdit;
        if (!id) return;
        if (confirm(`Haluatko varmasti poistaa keikan ${eventDate}: ${venueName}?`)) {
            firebase.database().ref(`/events/${id}`).remove().then((err: any) => {
                if (!err) {
                    this.setState((prevState: IState) => {
                        const updatedGigs = prevState.gigs.filter((gig: IGig) => gig.id !== id);
                        return {
                            ...prevState,
                            gigs: updatedGigs,
                            gigToEdit: undefined,
                        }
                    });
                }
            });
        }
    }

    render() {
        const { classes } = this.props;
        const { gigs, gigToEdit, loading } = this.state;
        return (
            <div className={classes.root}>
                <GigDialog
                    data={gigToEdit}
                    show={gigToEdit !== null}
                    onFieldChange={this.handleEditFieldChange}
                    onSave={this.handleEditSave}
                    onCancel={this.handleEditCancel}
                    onDelete={this.handleDelete}
                />
                <Button
                    variant="contained"
                    onClick={() => this.handleEdit(new Gig())}
                    className={classes.addButton}
                >
                    Lisää keikka
                </Button>
                {loading ? (
                    <div className={classes.loadingContainer}>
                        <CircularProgress />
                    </div>
                ) : (
                    <List component="nav">
                        {gigs.map((gig: any) => (
                            <ListItem
                                button
                                key={gig.id}
                                onClick={() => this.handleEdit(gig)}
                            >
                                <Avatar className={gig.eventType.substr(0,1) === 'e' ? classes.electricAvatar : ''}>{gig.eventType.substr(0,2).toUpperCase()}</Avatar>
                                <ListItemText primary={`${gig.eventDate}: ${gig.venueName}`} secondary={`${gig.venueCity}`} />
                                <OpenIcon className={classes.openIcon} />
                            </ListItem>
                        ))}
                    </List>
                )}
            </div>
        );
    }
}

export default withStyles(styles, {withTheme: true})(GigsPage);