import React, { Component, Fragment } from 'react';
import firebase from 'firebase';
import { CircularProgress, Table, TableBody, TableCell, TableHead, TableRow, Typography } from '@material-ui/core';
import { Theme, withStyles } from '@material-ui/core/styles';

const styles = (theme: Theme) => ({
    root: {
        // padding: theme.spacing.unit * 3,
        flexGrow: 1,
    },
    allHits: {
        padding: theme.spacing.unit * 2,
        paddingLeft: theme.spacing.unit * 3,
    },
    loading: {
        marginTop: theme.spacing.unit * 2,
        marginLeft: theme.spacing.unit * 2,
    },
});

class StatisticsPage extends Component<any, any> {
    state = {
        totalHits: 0,
        monthlyHits: [],
        loading: false,
    };

    componentDidMount() {
        this.setState({loading: true}, () => {
            const promises: Promise<any>[] = [];
            promises.push(firebase.database().ref('counters/all-hits-index').once('value').then(snapshot => {
                this.setState({
                    totalHits: snapshot.val(),
                });
            }));
            promises.push(firebase.database().ref('counters/monthly-hits-index').once('value').then(snapshot => {
                const valObj = snapshot.val();
                const monthlyValues = Object.keys(valObj).map((key: string) => {
                    return {
                        month: key,
                        count: valObj[key],
                    };
                });
                this.setState({
                    monthlyHits: monthlyValues,
                });
            }));
            Promise.all(promises).then(() => {
                this.setState({loading: false});
            });
        });
    }

    render() {
        const { classes } = this.props;
        const { totalHits, monthlyHits, loading } = this.state;
        return (
            <div className={classes.root}>
                {loading ? (
                    <CircularProgress className={classes.loading} />
                ) : (
                    <React.Fragment>
                        <Typography className={classes.allHits} variant="subtitle1">Kävijät yhteensä {totalHits} kpl</Typography>
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell>Kuukausi</TableCell>
                                    <TableCell>Kävijät</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {monthlyHits.sort((a: any, b: any) => {
                                    if (a.month < b.month) return 1;
                                    if (a.month > b.month) return -1;
                                    return 0;
                                }).map((m: any) => (
                                    <TableRow key={m.month}>
                                        <TableCell>{m.month}</TableCell>
                                        <TableCell>{m.count}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </React.Fragment>
                )}
            </div>
        );
    }
}

export default withStyles(styles, {withTheme: true})(StatisticsPage);