import React from 'react';
import {
    Button,
    Dialog,
    DialogTitle,
    DialogActions,
    DialogContent,
    FormControlLabel,
    Paper,
    Radio,
    RadioGroup,
    TextField,
    Theme,
    Typography,
    withStyles,
} from '@material-ui/core'

interface IProps {
    show: boolean;
    data: any;
    width?: any;
    onSave: (event: React.MouseEvent) => void;
    onCancel: (event: React.MouseEvent) => void;
    onDelete: () => void;
    onFieldChange: (fieldName: string, value: any) => void;
    classes: any,
}

const styles = (theme: Theme) => ({
    root: {
        backgroundColor: theme.palette.background.default,
    },
});

const GigDialog = (props: IProps) => {
    const { classes, show, data, onSave, onCancel, onFieldChange, onDelete } = props;
    if (!data) { return null; }

    let deleteButton = null;
    if (!!data.id) {
        deleteButton = <Button variant="text" color="secondary" onClick={onDelete}>Poista</Button>;
    }

    return (
        <Dialog
            PaperProps={{
                classes: {
                    root: classes.root,
                },
            }}
            open={show}
            // fullWidth={true}
            fullScreen
            maxWidth={'sm'}
        >
            <DialogTitle>Keikan tiedot</DialogTitle>
            <DialogContent>
                <Paper elevation={1} style={{padding: '10px'}}>
                    <Typography variant="h6">Keikkapäivä *</Typography>
                    <TextField
                        name="eventDate"
                        label=""
                        type="date"
                        fullWidth
                        value={data.eventDate || ''}
                        onChange={(evt: any) => onFieldChange(evt.target.name, evt.target.value)}
                    />
                </Paper>
                <Paper elevation={1} style={{padding: '10px', marginTop: '10px'}}>
                    <Typography variant="h6">Keikan tyyppi *</Typography>
                    <div>
                        <RadioGroup
                            name="eventType"
                            onChange={(evt: any) => onFieldChange(evt.target.name, evt.target.value)}
                            value={data.eventType}
                        >
                            <FormControlLabel
                                control={<Radio />}
                                checked={data.eventType === 'electric' || !data.eventType}
                                value="electric"
                                label="Sähköinen"
                            />
                            <FormControlLabel
                                control={<Radio />}
                                checked={data.eventType === 'acoustic'}
                                value="acoustic"
                                label="Akustinen"
                            />
                        </RadioGroup>
                    </div>
                </Paper>
                <Paper elevation={1} style={{padding: '10px', marginTop: '10px'}}>
                    <Typography variant="h6">Keikkapaikka</Typography>
                    <TextField
                        name="venueName"
                        label="Paikka tai 'Private' *"
                        fullWidth
                        value={data.venueName}
                        onChange={(evt: any) => onFieldChange(evt.target.name, evt.target.value)}
                    />
                    <TextField
                        name="venueAddress"
                        label="Katuosoite"
                        fullWidth
                        value={data.venueAddress}
                        onChange={(evt: any) => onFieldChange(evt.target.name, evt.target.value)}
                    />
                    <TextField
                        name="venueCity"
                        label="Kaupunki *"
                        fullWidth
                        value={data.venueCity}
                        onChange={(evt: any) => onFieldChange(evt.target.name, evt.target.value)}
                    />
                </Paper>
                <Paper elevation={1} style={{padding: '10px', marginTop: '10px', marginBottom: '10px'}}>
                    <Typography variant="h6">Lisätiedot</Typography>
                    <TextField
                        name="desc"
                        label="Lisätiedot"
                        fullWidth
                        value={data.desc}
                        onChange={(evt: any) => onFieldChange(evt.target.name, evt.target.value)}
                    />
                    <TextField
                        name="venueUrl"
                        label="Nettiosoite"
                        type="url"
                        fullWidth
                        value={data.venueUrl}
                        onChange={(evt: any) => onFieldChange(evt.target.name, evt.target.value)}
                    />
                </Paper>
            </DialogContent>
            <DialogActions>
                {deleteButton}
                <Button
                    variant="contained"
                    color="default"
                    onClick={onCancel}
                >
                    Keskeytä
                </Button>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={onSave}
                    disabled={data.eventDate === '' || data.venueName === '' || data.venueCity === ''}
                >
                    Tallenna
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default withStyles(styles)(GigDialog);